body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.whiteSquare {
  display: flex;
  align-items: center;
  justify-content: center;
  fill: white;
  stroke: rgb(55,55,55);
  stroke-width: 1;
}

.blackSquare {
  display: flex;
  align-items: center;
  justify-content: center;
  fill: black;
  stroke: rgb(55,55,55);
  stroke-width: 1;
}

.highlightSquare {
  display: flex;
  align-items: center;
  justify-content: center;
  fill: yellow;
  stroke: rgb(55,55,55);
  stroke-width: 1;
}

.clueText {
  display: flex;
  align-items: center;
  justify-content: center;
  fill: black;
  font-size: 20px;
}

.clueNumber {
  display: flex;
  align-items: center;
  justify-content: center;
  fill: black;
  font-size: 10px;
}

.clueList {
  list-style: none;
  margin-left: 0;
   padding-left: 1em;
   text-indent: 0em;
}

.crossword {
  height: 500px;
  width: 500px;
}

.container {
  padding: 5%;
}

.extraWords {
  max-width: 450px;
  width: 450px;
}
